export const RadioButtons = ({themeList, setIsThemeChange}) => {
  const classes = useStyles();
  const [check, setChecked] = React.useState("false");
  const [selectedValue, setSelectedValue] = React.useState(themeList[0]);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedValue(event.target.value);
    setIsThemeChange(event.target.value);
  };

  const [state, setState] = React.useState({
    checkedA: true,
    checkedB: true,
    checkedF: true,
    checkedG: true,
  });

  const handleChangeE = (event: React.ChangeEvent<HTMLInputElement>) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };

  return (
    <div className={classes.paper}>
{
        themeList.map((item, index) => (
        <Radio style={{backgroundColor: colorItems[index]}}
      key={index}
        checked={selectedValue === item}
        onChange={handleChange}
        value={item}
        name="radio-button-demo"
      />
        ))
      }
      </div>
  );
}