function getSteps(index) {
  switch (index) {
    case 0:
      return "การเพิ่มนักเรียนเข้าสู่ชั้นเรียน";
    case 1:
      return "การประกาศภายในชั้นเรียน";
    case 2:
      return "การเพิ่มเอกสาร และสื่อการเรียนรู้";
    case 3:
      return "การมอบหมายกิจกรรมหรือภาระงาน";
    case 4:
      return "การสร้างภาระงานแบบถาม-ตอบ";
    case 5:
      return "การสร้างแบบทดสอบ";
    case 6:
      return "การตรวจงานนักเรียน";
    case 7:
      return "การบันทึกคะแนนนักเรียนเป็นไฟล์ Excel";
    case 8:
      return "ข้อมูลอื่น ๆ เพิ่มเติม";
    default:
      return "ติดต่อครูผู้ดูแลระบบ";
}
}
 
function getStepContent(step: number) {
  switch (step) {
    case 0:
      return <TopicO></TopicO>;
      case 1:
        return <MenuA></MenuA>;
        case 2:
          return <MenuE></MenuE>;
          case 3:
            return <MenuC></MenuC>;
            case 4:
              return <MenuD></MenuD>;
              case 5:
      return <MenuB></MenuB>;
      case 6:
      return <div></div>;
      case 7:
      return <div></div>;
      case 8:
      return <div></div>;
      
    default:
      return "ติดต่อครูผู้ดูแลระบบ";
  }
}

function getActiveContent(allTask) {
  switch (allTask) {
    case 0:
      return 0;
    case 1:
      return 1;
    case 2:
      return 2;
    case 3:
      return 3;
      case 4:
        return 4;
    default:
      return 4;
  }
}



