import ButtonGroup from "@material-ui/core/ButtonGroup";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";

import LinearProgress from "@material-ui/core/LinearProgress";

import pic01_01 from "../images/01-01.jpg"
import pic01_02 from "../images/01-02.jpg"
import pic01_03 from "../images/01-03.jpg"
import pic01_04 from "../images/01-04.jpg"

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(3),
    },
    formControlA: {
      margin: theme.spacing(3),
    },
    buttonA: {
      margin: theme.spacing(1, 1, 0, 0),
    },
    rootA: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2),
      },
    },
    image:{
      maxWidht: "50vh",
    },buttonNext: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"
    },
    
  })
);

function getSteps() {
  return [
    "เข้าสู่เว็บไซต์ Google",
    "กรอกชื่ออีเมล",
    "กรอกรหัสผ่าน",
    "เข้าสู่ระบบสำเร็จ",
  ];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
                เปิดบราวเซอร์แล้วพิมพ์ www.google.com หรือกด
        <a href="https://www.google.com" target="_blank">ที่นี่</a>
        จากนั้นกด "ลงชื่อเข้าสู่ระบบ"
        </Typography>
        <img src={pic01_01} style={{maxWidth: "100%"}}></img>
      </div>);
    case 1: 
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
                กรอกชื่ออีเมล คือ " ชื่อภาษาอังกฤษ.อักษรภาษาอังกฤษตัวแรกของนามสกุล@phukhieo.ac.th " จากนั้นกด "ถัดไป"
        </Typography>
        <img src={pic01_02} style={{maxWidth: "100%"}}></img>
      </div>);
    case 2:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
                กรอกรหัสผ่านที่ถูกต้อง จากนั้นกด "ถัดไป" (กรณีที่ลืมรหัสผ่าน ให้ติดต่อผู้ดูแลระบบ)
        </Typography>
        <img src={pic01_03} style={{maxWidth: "100%"}}></img>
      </div>);
    case 3:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
                เมื่อเข้าสู่ระบบเรียบร้อย วงกลมด้านบนขวาจะเป็นภาพของบัญชีหรือชื่อย่อของผู้ใช้
        </Typography>
        <img src={pic01_04} style={{maxWidth: "100%"}}></img>
      </div>);
    default:
      return "Unknown step";
  }
}
export default function TopicAA({setChildComplete}) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();



  const handleNext = () => {

    if(activeStep === steps.length - 1){
      setChildComplete(true);
    }
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
      <Stepper activeStep={activeStep} orientation="vertical" className={classes.root}>
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>
            <Typography variant="subtitle1" color="textDisabled" component="h6">
                {label}
        </Typography>
        </StepLabel>
            
            <StepContent>
              <Typography
              >{getStepContent(index)}</Typography>
                    
                        <ButtonGroup>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            ย้อนกลับ
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "เสร็จสิ้น"
                              : "ขั้นตอนถัดไป"}
                          </Button>
                        </ButtonGroup>
               
            </StepContent>
          </Step>
        ))}
        {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>จบวิธีการเข้าสู่ระบบ&apos; @phukhieo.ac.th</Typography>
          <Button onClick={handleReset} className={classes.button} variant="contained"
                            color="secondary">
            อ่านอีกครั้ง
          </Button>
        </Paper>
      )}
      </Stepper>
  );
}
