function getSteps() {
  return [
    "หน้าต่างชั้นเรียน Google Classroom",
    "หน้าต่างการตั้งค่าชั้นเรียน",
    "เมนูการเพิ่มนักเรียน",
    "เพิ่มนักเรียนด้วย Email นักเรียน",
  ];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
                แอพลิเคชันหน้าแรก 
                <li>เมนูบนซ้าย = กดเพื่อดูชั้นเรียนทั้งหมด</li>
                <li>เมนูบนขวา = กดเพื่อตั้งค่าชั้นเรียน</li>
                <li>Stream = กดเพื่อเข้าสู่หน้าแรกของชั้นเรียน</li>
                <li>Classwork = กดเพื่อดูภาระงานของชั้นเรียน</li>
                <li>People = กดเพื่อดูรายชื่อครูและนักเรียนในชั้นเรียน</li>
        </Typography>
        <img src={pic01_31} style={{maxWidth: "100%"}}></img>
      </div>);
    case 1: 
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
        กดเมนูบนขวา เพื่อตั้งค่าชั้นเรียน โดยครูผู้สอนสามารถแก้ไขข้อมูลชั้นเรียนได้ และสามารถเพิ่มนักเรียนเข้าสู่ชั้นเรียนโดยให้นักเรียนกรอก รหัสชั้นเรียน หรือ Class code
        </Typography>
        <img src={pic01_32} style={{maxWidth: "100%"}}></img>
      </div>);
    case 2:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
        ครูผู้สอนยังสามารถเพิ่มชั้นเรียนได้ผ่านเมนู People 
        </Typography>
        <img src={pic01_33} style={{maxWidth: "100%"}}></img>
      </div>);
    case 3:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
        ครูผู้สอนสามารถพิมพ์ Email นักเรียน หรือ คัดลอก Email นักเรียนหลายคนพร้อมกันจากไฟล์ Excel ได้
        </Typography>
        <img src={pic01_34} style={{maxWidth: "100%"}}></img>
      </div>);
    default:
      return "Unknown step";
  }
}
export default function TopicO() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
      <Stepper activeStep={activeStep} orientation="vertical" className={classes.root}>
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>
            <Typography variant="subtitle1" color="textDisabled" component="h6">
                {label}
        </Typography>
        </StepLabel>
            
            <StepContent>
              <Typography
              >{getStepContent(index)}</Typography>
                    
                        <ButtonGroup>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            ย้อนกลับ
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "เสร็จสิ้น"
                              : "ขั้นตอนถัดไป"}
                          </Button>
                        </ButtonGroup>
               
            </StepContent>
          </Step>
        ))}
        {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>เพิ่มนักเรียนสำเร็จ&apos;</Typography>
          <Button onClick={handleReset} className={classes.button} variant="contained"
                            color="secondary">
            อ่านอีกครั้ง
          </Button>
        </Paper>
      )}
      </Stepper>
  );
}
