import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";

import LinearProgress from "@material-ui/core/LinearProgress";

import pic02_01 from "../images/06-01.jpg"
import pic02_02 from "../images/06-02.jpg"
import pic02_03 from "../images/06-03.jpg"

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(0),
    },
    formControlA: {
      margin: theme.spacing(3),
    },
    buttonA: {
      margin: theme.spacing(1, 1, 0, 0),
    },
    rootA: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2),
      },
    },
    image:{
      maxWidht: "50vh",
    },buttonNext: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"
    },
    
  })
);

function getSteps() {
  return [
    "ข้อมูลการใช้งาน",
    "สร้างคำถาม",
    "ระบุรายละเอียด",
    "มอบหมายคำถาม",
  ];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="h6">
               ข้อมูลทั่วไป
        </Typography>
      </div>);
    case 1: 
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="h6">
               กดปุ่มสร้าง แล้วเลือก คำถาม (Question) 
        </Typography>
        <img src={pic02_01} style={{width: "100%"}}></img>
      </div>);
    case 2:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="h6">
                กรอกข้อมูลคำถามให้ครบถ้วน ได้แก่ หัวข้อ รายละเอียด และแนบไฟล์ที่เกี่ยวข้อง โดยคุณครูสามารถกำหนด คะแนนเต็ม วันที่กำหนดส่ง และหน่วยการเรียนรู้หรือหัวข้อเพิ่มเติมได้ ดังรูป
                ทั้งนี้ ยังสามรถกำหนดรูปแบบของคำถามได้ เช่น
                <li>รูปแบบคำตอบ ได้แก่ แบบอัตนัยและปรนัย</li>
                <li>การกำหนดขอบเขตการดูผลสรุปแบบทดสอบของผู้เรียน</li>
        </Typography>
        <img src={pic02_02} style={{width: "100%"}}></img>
      </div>);
    case 3:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="h6">
                เมื่อสร้างคำถามเสร็จแล้ว จะปรากฏในหน้า งานชั้นเรียน และ สตรีม โดยนักเรียนสามารถเปิดเข้าไปตอบคำถามตามรายละเอียดที่ระบุไว้ได้
        </Typography>
        <img src={pic02_03} style={{width: "100%"}}></img>
      </div>);
    default:
      return "Unknown step";
  }
}
export default function MenuD() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();



  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
      <Stepper activeStep={activeStep} orientation="vertical" className={classes.root}>
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>
            <Typography variant="subtitle1" color="textDisabled" component="h6" style={{ 
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"}}>
                {label}
        </Typography>
        </StepLabel>
            
            <StepContent>
              <Typography
              >{getStepContent(index)}</Typography>
                    
                        <ButtonGroup>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            ย้อนกลับ
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "เสร็จสิ้น"
                              : "ขั้นตอนถัดไป"}
                          </Button>
                        </ButtonGroup>
               
            </StepContent>
          </Step>
        ))}
        {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>จบวิธีการมอบหมายคำถาม&apos;</Typography>
          <Button onClick={handleReset} className={classes.button} variant="contained"
                            color="secondary">
            อ่านอีกครั้ง
          </Button>
        </Paper>
      )}
      </Stepper>
  );
}
