import React from "react";
import { makeStyles, Theme, createStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";

import LinearProgress from "@material-ui/core/LinearProgress";

import pic01_21 from "../images/01-21.jpg"
import pic01_22 from "../images/01-22.jpg"
import pic01_23 from "../images/01-23.jpg"
import pic01_24 from "../images/01-24.jpg"
import pic01_25 from "../images/01-25.jpg"

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(3),
    },
    formControlA: {
      margin: theme.spacing(3),
    },
    buttonA: {
      margin: theme.spacing(1, 1, 0, 0),
    },
    rootA: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2),
      },
    },
    image:{
      maxWidht: "50vh",
    }
  })
);

function getSteps() {
  return [
    "เปิดใช้งานแอพ",
    "เลือกบัญชีผู้ใช้",
    "สร้างชั้นเรียน",
    "ใส่ข้อมูลชั้นเรียน",
  ];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
                เปิดแอพพลิเคชัน แล้วเลือกเริ่มต้นใช้งาน
        </Typography>
        <img src={pic01_21} style={{maxWidth: "100%"}}></img>
      </div>);
    case 1: 
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
            เลือกบัญชีผู้ใช้
        </Typography>
        <img src={pic01_22} style={{maxWidth: "100%"}}></img>
      </div>);
    case 2:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
        จะเข้าชั้นเรียน Google Classroom ได้ จากนั้นกดที่เครื่องหมาย + แล้วเลือก "สร้างชั้นเรียน" 
        </Typography>
        <img src={pic01_23} style={{maxWidth: "100%"}}></img>
      </div>);
    case 3:
      return (<div>
        <Typography variant="h6" color="textDisabled" component="h6">
        กรอกชื่อ รายวิชา และชื่อกลุ่มผู้เรียน ดังรูปข้างล่าง และเมื่อสร้างห้องเรียนสำเร็จ จะได้ดังรูป
        </Typography>
        <img src={pic01_24} style={{maxWidth: "100%"}}></img>
        <img src={pic01_25} style={{maxWidth: "100%"}}></img>
      </div>);
    default:
      return "Unknown step";
  }
}
export default function TopicAB({setChildComplete}) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {

    if(activeStep === steps.length - 1){
      setChildComplete(true);
    }
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
      <Stepper activeStep={activeStep} orientation="vertical" className={classes.root}>
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>
            <Typography variant="subtitle1" color="textDisabled" component="h6">
                {label}
        </Typography>
        </StepLabel>
            
            <StepContent>
              <Typography
              >{getStepContent(index)}</Typography>
                    
                        <ButtonGroup>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            ย้อนกลับ
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "เสร็จสิ้น"
                              : "ขั้นตอนถัดไป"}
                          </Button>
                        </ButtonGroup>
               
            </StepContent>
          </Step>
        ))}
        {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>สร้างชั้นเรียนสำเร็จ&apos; ขั้นตอนต่อไป คือ การเพิ่มนักเรียนเข้าชั้นเรียน</Typography>
          <Button onClick={handleReset} className={classes.button} variant="contained"
                            color="secondary">
            อ่านอีกครั้ง
          </Button>
        </Paper>
      )}
      </Stepper>
  );
}
