import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";

import LinearProgress from "@material-ui/core/LinearProgress";

import pic02_01 from "../images/02-01.jpg"
import pic02_02 from "../images/02-02.jpg"
import pic02_03 from "../images/02-03.jpg"
import pic02_04 from "../images/02-04.jpg"

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(0),
    },
    formControlA: {
      margin: theme.spacing(3),
    },
    buttonA: {
      margin: theme.spacing(1, 1, 0, 0),
    },
    rootA: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2),
      },
    },
    image:{
      maxWidht: "50vh",
    },buttonNext: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"
    },
    
  })
);

function getSteps() {
  return [
    "ข้อมูลการใช้งาน",
    "เข้าสู่หน้าสตรีม",
    "กรอกข้อมูลที่ต้องการประกาศ",
    "แนบไฟล์เพิ่มเติม",
    "สร้างประกาศในชั้นเรียนสำเร็จ",
  ];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="p">
               ข้อมูลทั่วไป
        </Typography>
      </div>);
    case 1:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="p">
               ในหน้าสตรีมของชั้นเรียน ให้กดเลือกช่องสำหรับประกาศหรือประชาสัมพันธ์ให้แก่ชั้นเรียน
        </Typography>
        <img src={pic02_01} style={{maxWidth: "100%"}}></img>
      </div>);
    case 2: 
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="p">
               ใส่ข้อมูลที่ต้องการประกาศ โดยเมื่อกรอกข้อมูลเรียบร้อย ให้กดปุ่มเมนู "ส่ง" ที่อยู่ข้างเมน฿ "คลิปหนีบกระดาษ"
        </Typography>
        <img src={pic02_02} style={{maxWidth: "100%"}}></img>
      </div>);
    case 3:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="p">
                กรณีที่ต้องการแนบไฟล์เพิ่มเติม สามารถกดปุ่มเมนู "คลิปหนีบกระดาษ" โดยจะสามารถแนบไฟล์ได้จาก
                <li>Drive: จาก Google Drive</li>
                <li>Link: จาก การแนบ URL ของเว็บไซต์</li>
                <li>Camera: จาก ภาพถ่ายกล้องถ่ายรูป</li>
                <li>File: จาก ไฟล์ต่างๆ ที่อยู่ในเครื่อง</li>
                <li>Photo: จาก รูปภาพที่อยู่ในเครื่อง</li>
        </Typography>
        <img src={pic02_03} style={{maxWidth: "100%"}}></img>
      </div>);
    case 4:
      return (<div>
        <Typography variant="subtitle1" color="textDisabled" component="p">
                เมื่อประกาศสำเร็จ ก็จะได้ผลลัพธ์ดังรูป
        </Typography>
        <img src={pic02_04} style={{maxWidth: "100%"}}></img>
      </div>);
    default:
      return "Unknown step";
  }
}
export default function Menu1A() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();



  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
      <Stepper activeStep={activeStep} orientation="vertical" className={classes.root}>
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>
            <Typography variant="subtitle1" color="textDisabled" component="h6" style={{ 
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"}}>
                {label}
        </Typography>
        </StepLabel>
            
            <StepContent>
              <Typography
              >{getStepContent(index)}</Typography>
                    
                        <ButtonGroup>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            ย้อนกลับ
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === steps.length - 1
                              ? "เสร็จสิ้น"
                              : "ขั้นตอนถัดไป"}
                          </Button>
                        </ButtonGroup>
               
            </StepContent>
          </Step>
        ))}
        {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>สร้างประกาศในชั้นเรียนสำเร็จ&apos; </Typography>
          <Button onClick={handleReset} className={classes.button} variant="contained"
                            color="secondary">
            อ่านอีกครั้ง
          </Button>
        </Paper>
      )}
      </Stepper>
  );
}
